import {
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Renderer,
  SimpleChanges, Self, Optional,
} from '@angular/core';
import {Subscription} from 'rxjs/Subscription';
import {extendObject} from '../../utils/object-extend';

import {BaseFxDirective} from './base';
import {MediaChange} from '../../media-query/media-change';
import {MediaMonitor} from '../../media-query/media-monitor';
import {LayoutDirective, LAYOUT_VALUES} from './layout';

/**
 * 'layout-wrap' flexbox styling directive
 * Defines wrapping of child elements in layout container
 * Optional values: reverse, wrap-reverse, none, nowrap, wrap (default)]
 * @see https://css-tricks.com/almanac/properties/f/flex-wrap/
 */

export class LayoutWrapDirective extends BaseFxDirective implements OnInit, OnChanges, OnDestroy {
  private _layout = 'row';  // default flex-direction
  private _layoutWatcher: Subscription;

         set wrap(val)     { this._cacheInput("wrap", val); }
      set wrapXs(val)   { this._cacheInput('wrapXs', val); }
   set wrapGtXs(val) { this._cacheInput('wrapGtXs', val); };
      set wrapSm(val)   { this._cacheInput('wrapSm', val); };
   set wrapGtSm(val) { this._cacheInput('wrapGtSm', val); };
      set wrapMd(val)   { this._cacheInput('wrapMd', val); };
   set wrapGtMd(val) { this._cacheInput('wrapGtMd', val); };
      set wrapLg(val)   { this._cacheInput('wrapLg', val); };
   set wrapGtLg(val) { this._cacheInput('wrapGtLg', val); };
      set wrapXl(val)   { this._cacheInput('wrapXl', val); };

  constructor(
    monitor : MediaMonitor,
    elRef: ElementRef,
    renderer: Renderer,
      container: LayoutDirective) {

    super(monitor, elRef, renderer)

    if (container) {  // Subscribe to layout direction changes
      this._layoutWatcher = container.layout$.subscribe(this._onLayoutChange.bind(this));
    }
  }

  // *********************************************
  // Lifecycle Methods
  // *********************************************

  ngOnChanges(changes: SimpleChanges) {
    if (changes['wrap'] != null || this._mqActivation) {
      this._updateWithValue();
    }
  }

  /**
   * After the initial onChanges, build an mqActivation object that bridges
   * mql change events to onMediaQueryChange handlers
   */
  ngOnInit() {
    this._listenForMediaQueryChanges('wrap', 'wrap', (changes: MediaChange) =>{
      this._updateWithValue(changes.value);
    });
    this._updateWithValue();
  }


  // *********************************************
  // Protected methods
  // *********************************************

  /**
   * Cache the parent container 'flex-direction' and update the 'flex' styles
   */
  private _onLayoutChange(direction) {
    this._layout = (direction || '').toLowerCase().replace('-reverse', '');
    if (!LAYOUT_VALUES.find(x => x === this._layout)) {
      this._layout = 'row';
    }

    this._updateWithValue();
  }

  private _updateWithValue(value?: string) {
    value = value || this._queryInput("wrap") || 'wrap';
    if (this._mqActivation) {
      value = this._mqActivation.activatedInput;
    }
    value = this._validateValue(value);

    this._applyStyleToElement(this._buildCSS(value));
  }


  /**
   * Build the CSS that should be assigned to the element instance
   */
  private _buildCSS(value) {
    return extendObject({ 'flex-wrap': value }, {
      'display' : 'flex',
      'flex-direction' : this._layout || 'row'
    });
  }

  /**
   * Convert layout-wrap="<value>" to expected flex-wrap style
   */
  private _validateValue(value) {
    switch (value.toLowerCase()) {
      case 'reverse':
      case 'wrap-reverse':
        value = 'wrap-reverse';
        break;

      case 'no':
      case 'none':
      case 'nowrap':
        value = 'nowrap';
        break;

      // All other values fallback to "wrap"
      default:
        value = 'wrap';
        break;
    }
    return value;
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{selector: `
  [fxLayoutWrap],
  [fxLayoutWrap.xs]
  [fxLayoutWrap.gt-xs],
  [fxLayoutWrap.sm],
  [fxLayoutWrap.gt-sm]
  [fxLayoutWrap.md],
  [fxLayoutWrap.gt-md]
  [fxLayoutWrap.lg],
  [fxLayoutWrap.gt-lg],
  [fxLayoutWrap.xl]
`}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: MediaMonitor, },
{type: ElementRef, },
{type: Renderer, },
{type: LayoutDirective, decorators: [{ type: Optional }, { type: Self }, ]},
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'wrap': [{ type: Input, args: ['fxLayoutWrap', ] },],
'wrapXs': [{ type: Input, args: ['fxLayoutWrap.xs', ] },],
'wrapGtXs': [{ type: Input, args: ['fxLayoutWrap.gt-xs', ] },],
'wrapSm': [{ type: Input, args: ['fxLayoutWrap.sm', ] },],
'wrapGtSm': [{ type: Input, args: ['fxLayoutWrap.gt-sm', ] },],
'wrapMd': [{ type: Input, args: ['fxLayoutWrap.md', ] },],
'wrapGtMd': [{ type: Input, args: ['fxLayoutWrap.gt-md', ] },],
'wrapLg': [{ type: Input, args: ['fxLayoutWrap.lg', ] },],
'wrapGtLg': [{ type: Input, args: ['fxLayoutWrap.gt-lg', ] },],
'wrapXl': [{ type: Input, args: ['fxLayoutWrap.xl', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
